import axios from "axios";
export const getUser = async (git_username) => {
    try {
        const { data } = await axios.get(`https://api.github.com/users/${git_username}`);
        return data;
    }
    catch (error) {
        console.error(error);
        throw new Error(error);
    }
};
export const getActivity = async (git_username) => {
    try {
        const { data } = await axios.get(`https://api.github.com/users/${git_username}/events`);
        return data;
    }
    catch (error) {
        console.error(error);
        throw new Error(error);
    }
};
